  font-family: 'Montserrat', sans-serif;
}
.app{
  padding: 80px;
  background-color: #3aafa9;  
}
.react-tabs {
  -webkit-tap-highlight-color: transparent;
}

.react-tabs__tab-list {
  /*border-bottom: 1px solid #aaa;*/
  margin: 0 0 30px;
  padding: 0;
  border: 2px solid #17252a;
  padding-left: 35px;
  background-color: #def2f1;
  border-radius: 10px;
}

.react-tabs__tab {
  display: inline-block;
  border: 1px solid transparent;
  border-bottom: none;
  bottom: -1px;
  position: relative;
  list-style: none;
  padding: 6px 100px;
  margin-bottom: 20px;
  margin-top: 20px;
  cursor: pointer;
  font-size: 20px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 800;
  text-align: center;
  color: #17252a;
  
}

.react-tabs__tab--selected {
  background: #17252a;
  border-color: #17252a;
  color: #feffff;
  border-radius: 5px 5px 0 0;
}

.react-tabs__tab:focus {
  outline: none;
}

.react-tabs__tab:focus:after {
  content: '';
  position: absolute;
  height: 5px;
  left: -4px;
  right: -4px;
  bottom: -5px;
  background: #17252a;
}

.react-tabs__tab-panel {
  display: none;
}

.react-tabs__tab-panel--selected {
  display: block;
}

